import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import schedules from "./schedules";
import Offcanvas from 'react-bootstrap/Offcanvas';
import { handleClose, setcalsizeValue, setmajorValue, setmodeValue, setteamValue, dmClose, dmOpen } from './store';
import { Button, ButtonGroup, Nav, Modal, Container} from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import ToggleButton from 'react-bootstrap/ToggleButton';
import styled from 'styled-components';
const TYPES = [
    { name: '픽스', value: '1', var:'success', bg:'#198754',color:'white'},
    { name: '수술', value: '2', var:'danger',bg: '#dc3545',color:'white'},
    { name: '공지', value: '3', var:'warning',bg:'#ffc107',color:'white'},
    { name: 'ST', value: '4', var:'primary',bg:'#0d6efd',color:'white'},
    { name: '키핑', value: '5', var:'secondary',bg:'#6c757d',color:'white'},
    { name: '풀매칭', value: '6', var:'secondary',bg:'#6c757d',color:'white'},
    { name: '개인', value: '7', var:'secondary',bg:'#6c757d',color:'white'}
];
const MAJORS = ['외과', '보철', '보존', '소치','치주', '교정', '내과', '영상' ];
const MODES = [
    { name: '조별', value: '1', var:'outline-success'},
    { name: '과별', value: '2', var:'outline-danger' },
    { name: '개인', value: '3', var:'outline-primary' }
];
const CALSIZES = [
    { name: '한달', value: '1', var:'outline-secondary'},
    { name: '2주', value: '2', var:'outline-secondary' }
];

function daySchedules(thisDate, thisSchedule){

    let days = thisSchedule.filter(function(x){
        let sdate =new Date(x.time);
        return sdate.getDate() === thisDate.getDate() && sdate.getFullYear() === thisDate.getFullYear() && sdate.getMonth() === thisDate.getMonth();
    })
    
    return days;
}
function month(date){
    let thisMonth = [];
    date.setDate(1);
    thisMonth.push(new Date(date));
    thisMonth.push(new Date(date.setDate(date.getDate()+7)));
    thisMonth.push(new Date(date.setDate(date.getDate()+7)));
    thisMonth.push(new Date(date.setDate(date.getDate()+7)));
    thisMonth.push(new Date(date.setDate(date.getDate()+7)));
    thisMonth.push(new Date(date.setDate(date.getDate()+7)));
    return thisMonth;
}
function twoWeeks(date){
    let thisTwoWeek = [];
    thisTwoWeek.push(new Date(date));
    thisTwoWeek.push(new Date(date.setDate(date.getDate()+7)));
    return thisTwoWeek;
}
function dateColor(i,m){
    const a = {
        color:'black', 
        opacity:'100%', 
        fontWeight:'500', 
        height:'80px', 
        borderTop:'1px solid #DDDDDD',
    };

    if(i===0) a.color = '#F24C4C';
    else if(i===6) a.color = '#2155CD';
    if(!m) a.opacity = '50%';
    
    return a;
}
function cbodyStyle(i){
    const a = {
        fontSize:'10px', 
        verticalAlign: 'top',
        textAlign: 'left'
    };

    if(i==='1') a.fontSize = '10px'
    else a.fontSize = '12px'
    
    return a;
}
function Calender(){
    let [now, setNow] = useState(new Date());
    let students = useSelector((state)=>state.students);
    let show = useSelector((state)=>state.show);
    let modeValue = useSelector((state)=>state.modeValue);
    let calsizeValue = useSelector((state)=>state.calsizeValue);
    let majorValue = useSelector((state)=>state.majorValue);
    let teamValue = useSelector((state)=>state.teamValue);
    let user = useSelector((state)=>state.user);
    let dmshow = useSelector((state)=>state.dmshow);
    let dispatch = useDispatch();
    let fData = [];

    if(modeValue=='1'){ //조별 일정 필터
        let teamStudent = students.filter(function(x){
            return x.team == teamValue;
        })
        
        let setA = new Set();
        for(var i=0;i<teamStudent.length;i++){
            setA = new Set([...setA , ...teamStudent[i].plans])
        }
        [...setA].map((a,i)=>{
            fData.push(schedules.find(function(x){
                return x.id == a;
            }))
        })
    }
    else if(modeValue=='2'){ //과별 일정 필터
        fData = [];
        fData = schedules.filter(function(x){
            return x.major == MAJORS[majorValue];
        })
    }
    else{ //개인 일정 필터
        fData = [];
        user.plans.map((a,i)=>{
            fData.push(schedules.find(function(x){
                return x.id == a;
            }))
        })
    }
    fData.sort(function(a,b){ //시간별 정렬
        if(new Date(a.time)>new Date(b.time)){
            return 1;
        }
        if(new Date(a.time)<new Date(b.time)){
            return -1;
        }
        return 0;
    })
    return(
        <div>
            {dmshow?<DM fData={fData}></DM>:null}
            <Setting></Setting>
            {
                calsizeValue=='1'
                ?(
                <ButtonGroup>
                    <Button size='sm' variant='light' onClick={()=> setNow(new Date(now.setMonth(now.getMonth()-1)))}> {'<'} </Button>
                    <Button size='sm' variant='light' onClick={()=>setNow(new Date())}> {now.getFullYear() + '년 '}{now.getMonth()+1}월</Button>
                    <Button size='sm' variant='light' onClick={()=> setNow(new Date(now.setMonth(now.getMonth()+1)))}> {'>'} </Button>
                </ButtonGroup>
                )
                :(
                <ButtonGroup>
                    <Button size='sm' variant='light' onClick={()=> setNow(new Date(now.setDate(now.getDate()-7)))}> {'<'} </Button>
                    <Button size='sm' variant='light' onClick={()=>setNow(new Date())}> {now.getFullYear() + '년 '+(now.getMonth()+1)+'월 '+parseInt((now.getDate()+now.getDay())/7+1)}주차</Button>
                    <Button size='sm' variant='light' onClick={()=> setNow(new Date(now.setDate(now.getDate()+7)))}> {'>'} </Button>
                </ButtonGroup>
                )
            }
           
        <table className="calender" style={{width:'100%', tableLayout:'fixed'}}>
            <thead>
                <tr style={{fontSize:'12px',textAlign:'left'}}>
                    <th style={{color:'red'}}>일</th><th>월</th><th>화</th><th>수</th><th>목</th><th>금</th><th style={{color:'#2155CD'}}>토</th>
                </tr>
            </thead>
            <tbody style={cbodyStyle(calsizeValue)}>
                {
                    calsizeValue=='1'
                    ?(
                        month(new Date(now)).map((a,i)=>{
                            return(
                            <Week a={a} now={now} fData={fData}/>
                            )
                            
                        })
                    )
                    :(
                        twoWeeks(new Date(now)).map((a,i)=>{
                            return(
                            <Week a={a} now={now} fData={fData}/>
                            )
                            
                        })
                    )
                }
            </tbody>
            
        </table>
        </div>
    )
    
}
function Week(props){
    let thisWeek = [];
    let dispatch = useDispatch();
    thisWeek.push(new Date(props.a.setDate(props.a.getDate()-props.a.getDay())));
    for(var i=0;i<6;i++){
        thisWeek.push(new Date(props.a.setDate(props.a.getDate()+1)));
    }
    return (
        <tr>
            {
            thisWeek.map((b,i)=>{
                return(
                    <td 
                    style={dateColor(b.getDay(),b.getMonth()==props.now.getMonth())}
                    onClick={()=>{
                        dispatch(dmOpen(b))
                    }}
                    >
                        {
                        b.getDate()==1
                        ?b.getMonth()+1+'/'+b.getDate()
                        :b.getDate()
                        }
                        <Plans b ={b} fData ={props.fData}/>
                    </td>
                )
            })
            }
        </tr>
        
    );
}
function Plans(props){
    return(
        daySchedules(props.b, props.fData).map((c,i)=>{
            return <Plan c={c} fData ={props.fData}/>
        })
    );
}
function Plan(props){
    let thistype = TYPES.find(function(x){return x.name==props.c.type});
    if(props.c.type=='픽스'){
        return(
            <div style={{textAlign:'left'}}>
                <div style={{borderRadius:'4px', border:'0.5px solid white',color:`${thistype.color}`, display:'inline-block', background:`${thistype.bg}`,paddingLeft:'1px',paddingRight:'1px'}}>{props.c.title.substr(1,1) + props.c.type.substr(0,1)}</div>
                {props.c.who.map((d,i)=>{
                     return <div style={{display:'inline-block'}}>{d}</div>
                })}
                
            </div>
        )
    }

    else{
        return(
            <div style={{textAlign:'left'}}>
                <div style={{borderRadius:'4px', border:'0.5px solid white',color:`${thistype.color}`, display:'inline-block', background:`${thistype.bg}`,paddingLeft:'1px',paddingRight:'1px'}}>{new Date(props.c.time).toTimeString().substr(0,5)}</div>
                {props.c.who.map((d,i)=>{
                     return <div style={{display:'inline-block'}}>{d}</div>
                })}
                <div style={{color:'#777777',display:'block', whiteSpace:'nowrap', overflow:'hidden',textOverflow:'ellipsis'}}>{props.c.title}</div>
            </div>
        )
    }
}
function Setting(){
    let show = useSelector((state)=>state.show);
    let modeValue = useSelector((state)=>state.modeValue);
    let calsizeValue = useSelector((state)=>state.calsizeValue);
    let majorValue = useSelector((state)=>state.majorValue);
    let teamValue = useSelector((state)=>state.teamValue);
    let dispatch = useDispatch();
    
    return(
        <Offcanvas show={show} onHide={()=>{dispatch(handleClose())}}>
            <Offcanvas.Header style={{borderBottom:'1px solid #DDDDDD'}} closeButton>
                <Offcanvas.Title>캘린더 설정</Offcanvas.Title>
            </Offcanvas.Header>
            <Offcanvas.Body style={{position:'relative'}}>
                <ButtonGroup style={{width:'100%'}}>
                {CALSIZES.map((calsize, idx) => (
                        <ToggleButton
                            key={calsize.name}
                            id={`radio-${calsize.name}`}
                            type="radio"
                            variant={calsize.var}
                            value={calsize.value}
                            checked={calsizeValue === calsize.value}
                            onChange={(e) => dispatch(setcalsizeValue(e.currentTarget.value))}
                        >
                        {calsize.name}
                        </ToggleButton>
                    ))}
                </ButtonGroup>
                <hr/>
                <ButtonGroup style={{width:'100%'}}>
                    {MODES.map((mode) => (
                        <ToggleButton
                            key={mode.name}
                            id={`radio-${mode.name}`}
                            type="radio"
                            variant={mode.var}
                            value={mode.value}
                            checked={modeValue == mode.value}
                            onChange={(e) => dispatch(setmodeValue(e.currentTarget.value))}
                        >
                        {mode.name}
                        </ToggleButton>
                    ))}
                </ButtonGroup>
                {
                    modeValue=='1'
                    ?(<ButtonGroup style={{display:'inline-block', marginTop:'10px'}}>
                        {
                            [1,2,3,4,5,6,7,8].map((a)=>{
                                {return <ToggleButton 
                                    style={{width:'25%', border:'0px', borderRadius:'20px'}} 
                                    key={a}
                                    id={`radio-${a}`}
                                    type="radio"
                                    variant='outline-secondary'
                                    value= {a}
                                    checked={teamValue === a}
                                    onChange={(e) => dispatch(setteamValue(a))}
                                >
                                    {a}조
                                 </ToggleButton>}
                            })
                        }
                    </ButtonGroup>
                    )
                    :(
                        modeValue=='2'
                        ?(<ButtonGroup style={{display:'inline-block', marginTop:'10px'}}>
                        {
                            MAJORS.map((major, idx) => (
                                <ToggleButton
                                    style={{width:'25%', border:'0px', borderRadius:'20px'}} 
                                    key={major}
                                    id={`radio-${major}`}
                                    type="radio"
                                    variant='outline-secondary'
                                    value={idx}
                                    checked={majorValue == idx}
                                    onChange={(e) => dispatch(setmajorValue(e.currentTarget.value))}
                                >
                                {major}
                                </ToggleButton>
                            ))
                        }
                    </ButtonGroup>
                    )
                        :<div>
                            <Button variant='light' style={{marginTop:'10px',marginBottom:'10px', width:'100%', border:'1px solid #DDDDDD'}}>개인일정 추가</Button>
                            <div style={{color:'#777777', fontSize:'12px'}}><span style={{background:'#777777', color:'#FFFFFF',padding:'2px', borderRadius:'5px'}}>tip</span> 당신만 볼 수 있는 일정이 추가됩니다</div>
                        </div>
                    )
                }
                <hr/>
                <ButtonGroup style={{display:'inline-block',width:'100%'}}>
                    <Button variant='light' style={{background:'#E8F9FD',width:'50%', height:'80px', border:'3px solid #FFFFFF'}}>차출하기</Button>
                    <Button variant='light' style={{background:'#EFF9FD',width:'50%', height:'80px', border:'3px solid #FFFFFF'}}>픽스메이커</Button>
                    <Button variant='light' style={{background:'#EFF9FD',width:'50%', height:'80px', border:'3px solid #FFFFFF'}}>수술등록</Button>
                    <Button variant='light' style={{background:'#E8F9FD',width:'50%', height:'80px', border:'3px solid #FFFFFF'}}>일괄등록</Button>
                    <div style={{color:'#777777', fontSize:'12px', marginBottom:'2px'}}><span style={{background:'#777777', color:'#FFFFFF',padding:'2px', borderRadius:'5px'}}>tip</span> 차출하기: 일정이 없는 인원을 차출이 필요한 일정에 배정합니다.</div>
                    <div style={{color:'#777777', fontSize:'12px', marginBottom:'2px'}}><span style={{background:'#777777', color:'#FFFFFF',padding:'2px', borderRadius:'5px'}}>tip</span> 픽스메이커: 우리 조의 픽스 일정을 만들어드립니다.</div>
                    <div style={{color:'#777777', fontSize:'12px', marginBottom:'2px'}}><span style={{background:'#777777', color:'#FFFFFF',padding:'2px', borderRadius:'5px'}}>tip</span> 수술등록: 수술일정을 등록하고, 임소위에게 알림을 보냅니다.</div>
                    <div style={{color:'#777777', fontSize:'12px', marginBottom:'2px'}}><span style={{background:'#777777', color:'#FFFFFF',padding:'2px', borderRadius:'5px'}}>tip</span> 일괄등록: 일정을 일괄적으로 등록합니다.</div>
                </ButtonGroup>
                <hr/>
            </Offcanvas.Body>
        </Offcanvas>
    )
}

function DM(props){
    let dmshow = useSelector(state=>state.dmshow)
    let dispatch = useDispatch();
    const Container = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 100;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    `;
    const Background = styled.div`
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.3);
    animation: modal-bg-show 0.3s;
    @keyframes modal-bg-show {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
`;
    const ModalBlock = styled.div`
        position: absolute;
        top: 6.5rem;
        border-radius: 20px;
        background-color: white;
        width: 20rem;
        min-height: 35rem;
        animation: modal-show 0.3s;
        @keyframes modal-show {
            from {
                opacity: 0;
                margin-top: +50px;
            }
            to {
                opacity: 1;
                margin-top: 0;
            }
        }
    `;
    const Contents = styled.div`
        
        display: flex;
        flex-direction: column;
        align-items: left;
    `;
    const dc = dateColor(new Date(dmshow).getDay(),1).color;
    return(
        <Container>
            <Background onClick={()=>{dispatch(dmClose())}}></Background>
            <ModalBlock>
                <header style={{textAlign:'left', borderBottom:'1px solid #DDDDDD'}}>
                    <div style={{display:'inline-block',color:`${dc}`, fontSize:'25px', padding:'10px',paddingBottom:'0px'}}>{new Date(dmshow).getDate()}</div>
                    <div style={{display:'inline-block',color:`${dc}`}}>
                        {['일','월','화','수','목','금','토','일'][new Date(dmshow).getDay()]}요일
                    </div>
                </header>
                <Contents>
                    {
                        daySchedules(new Date(dmshow),props.fData).map((a,i)=>{
                            let thistype = TYPES.find(function(x){return x.name==a.type});
                            return (
                            <Button variant={'outline-'+thistype.var} style={{
                                gridTemplateColumns:'50px auto 1fr', 
                                gridTemplateRows:'1fr 1fr', 
                                display:'grid',
                                paddingLeft:'5px',
                                border:'0px',
                                borderRadius:'15px',
                                borderBottom:'1px solid #DDDDDD'
                            }}>
                                {
                                    a.type=='픽스'
                                    ?(<div style={{fontSize:'11px',fontWeight:'700', gridColumn:'1 / 2',gridRow:'1 / 2',borderRight:'1px solid #DDDDDD'}}>
                                        {a.type}{a.title}
                                    </div>)
                                    :(<div style={{fontSize:'11px',fontWeight:'700', gridColumn:'1 / 2',gridRow:'1 / 2',borderRight:'1px solid #DDDDDD'}}>
                                        {new Date(a.time).toTimeString().substr(0,5)}
                                    </div>)
                                }
                                
                                <div style={{fontSize:'11px',fontWeight:'500', gridColumn:'1 / 2',gridRow:'2 / 3',borderRight:'1px solid #DDDDDD'}}>
                                    {a.major}
                                </div>
                                {
                                    a.type=='픽스'
                                    ?(<div style={{marginLeft:'5px',fontWeight:'700',display:'flex',gridColumn:'2 / 4',gridRow:'1/3'}}>{a.who.join()}</div>
                                    )
                                    :<div style={{marginLeft:'5px',fontWeight:'700',display:'flex',gridColumn:'2 / 3',gridRow:'1/3'}}>{a.who.join()}</div>
                                }
                                {
                                    a.type=='픽스'
                                    ?null
                                    :<div style={{display:'flex',gridColumn:'3 / 4',gridRow:'1/3', marginLeft:'10px',fontSize:'12px'}}>{a.title}</div>
                                }
                            </Button>
                            )
                        })
                    }
                </Contents>
                
            </ModalBlock>
        
      </Container>
    )
}
export default Calender;