import { useNavigate } from "react-router-dom";
import { Table } from "react-bootstrap";
import { useSelector } from "react-redux";

function Notice(){
    let navigate = useNavigate();
    let noti = useSelector((state)=>state.notices);
    let students = useSelector((state)=>state.students);
    function fullName(x){
        let z = students.find(function(y){
            return y.이름 == x
        })
        return (z.성 + z.이름)
    }
    return(
        <div>
            <div className="page-name">공지사항</div>
            <Table hover size="sm">
            <thead><tr><th>과</th><th>제목</th><th>작성자</th><th>작성일</th></tr></thead>
            <tbody>
                {
                    noti.map(function(a,i){
                        return(
                            <tr>
                                <td style={{width:'100px'}}>{a.과}</td>
                                <td>{a.title}</td>
                                <td style={{width:'100px'}}>{fullName(a.writer)}</td>
                                <td style={{width:'100px'}}>{new Date(a.date).toLocaleDateString().substring(2)}</td>
                            </tr>
                        )
                    })
                }
            </tbody>
            </Table>
        </div>
    )
}

  export default Notice;