import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import data from './data.js';
import { useState } from 'react';
import {Routes, Route} from 'react-router-dom'
import SubPage from './pages/SubPage';
import Fix from './pages/Fix';
import student from './student';
import List from './pages/List';
import Notice from './pages/Notice';
import Calender from './Calender';
import { Button, Modal, Form, Alert } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { handleShow, setmodeValue, setteamValue } from './store';
import { userLogin} from './store';

function App() {
  let [posts] =useState(data);
  let user = useSelector((state)=>state.user);
  const [show, setShow] = useState(false);
  let dispatch = useDispatch();
  if(!user.id){ //유저로그인 확인해 로컬스토리지 값 가져오기
    let savedId = localStorage.getItem('id');
    let savedPw = localStorage.getItem('pw');
    let savedUser = student.find(function(x){return x.id==savedId})
    if(savedUser && (savedUser.pw==savedPw)){
      dispatch(userLogin(savedUser));
      dispatch(setmodeValue('1'));
      dispatch(setteamValue(savedUser.team));
    }
  }

  return (
    <div className="App">
      <Login></Login>
      <Navbar collapseOnSelect expand="lg" variant="light" style={{background:'#FFFFFF'}}>
        <Button style={{width:'60px',height:'40px', fontSize:'20px', background:'white',color:'#777777',fontWeight:'550',border:'0px'}} 
              variant="light" onClick={()=>{dispatch(handleShow())}}>&#5171;</Button>
          <Navbar.Brand href="/" style={{fontWeight:'600'}}>덴트케어</Navbar.Brand>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" className="NavTog"/>
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link href="/fix">픽스일정</Nav.Link>
            </Nav>
            <Nav>
              <Nav.Link href="/list/main">조별 명단</Nav.Link>
              <Nav.Link href="/notice">공지사항</Nav.Link>
              <Nav.Link href="/mypage">마이페이지</Nav.Link>
            </Nav>
          </Navbar.Collapse>
      </Navbar>
      <Routes>
        <Route path="/" element={<Calender/>}/>
        <Route path="/fix" element={<Fix/>}/>
        <Route path="/surg" element={<SubPage posts = {posts}/>}/>
        <Route path="/posts/:id" element ={<SubPage posts = {posts}/>}/>
        <Route path="/list/:team" element={<List/>}/>
        <Route path='/notice' element={<Notice/>}/>
        <Route path='/mypage' element={<></>}/>
        <Route path="*" element={<div>404Page</div>}/>
      </Routes>
    </div>
    
  );
}

function Login(){
  let user = useSelector((state)=>state.user);
  let dispatch = useDispatch();
  const [error, setError] = useState(false)
  const [inputId, setInputId] = useState('')
  const [inputPw, setInputPw] = useState('')
  const handleInputId = (e) => {
    setInputId(e.target.value)
  }
  const handleInputPw = (e) => {
    setInputPw(e.target.value)
  }
  

  return(
    <Modal show={!user.id} backdrop="static" centered>
        <Modal.Body>
          <Modal.Header>
            <Modal.Title>덴트케어 로그인</Modal.Title>
          </Modal.Header>
          <Form>
            <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
              <Form.Label>학번</Form.Label>
              <Form.Control
                placeholder="20XXXXXX"
                value ={inputId}
                onChange={handleInputId}
                autoFocus
              />
            </Form.Group>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>비밀번호</Form.Label>
              <Form.Control 
              placeholder="password"
              value ={inputPw}
              onChange={handleInputPw} />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicCheckbox">
              <Form.Check type="checkbox" label="자동 로그인" />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          {error?<Alert variant='danger' style={{lineHeight:'0px'}}>로그인 정보가 틀렸습니다.</Alert>:null}
          <Button variant="light" type="submit" onClick={()=>{
            let inputUser = student.find(function(x){return x.id==inputId})
            if(!inputUser || !(inputUser.pw==inputPw)) setError(true);
            else{
              dispatch(userLogin(inputUser));
              dispatch(setmodeValue('1'));
              dispatch(setteamValue(inputUser.team));
              localStorage.setItem('id',inputId);
              localStorage.setItem('pw',inputPw);
            }
          }
            }>
            로그인
          </Button>
        </Modal.Footer>
      </Modal>
  )
}
export default App;
