import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { useState } from "react";
import { Nav } from 'react-bootstrap';
import { useSelector } from 'react-redux';


function List(){
    let [nowTeam, setTeam] = useState(0);
    let students = useSelector((state)=>state.students);
    let thisStudent = students.filter(function(x){
        return x.team == nowTeam;
    });

    return(
        <>
        <div className="page-name">조별 명단</div>
        <ButtonGroup style={{marginTop:'10px'}}>
            {
            [1,2,3,4,5,6,7,8].map(function(a,i){
                return(
                    <Button size= 'sm' variant={nowTeam==a?'dark':'secondary'} onClick={()=>{setTeam(a)}}>{a}조</Button>
                )
            })
            }
        </ButtonGroup>
        {
            nowTeam!=0
            ? <div style={{margin:'12px'}}>
                {
                thisStudent.map(function(a,i){
                    return(
                        <div style={{margin:'auto',width:'200px',fontSize:'15px', color:'#444444', borderBottom:'1px solid #DDDDDD'}}>{a.id} {a.성}{a.이름}</div>
                    )
                })
                }
            </div>
            :null
        }
        </>
    )
    
}
function ListMain(){

}
export default List;