import { configureStore, createSlice } from "@reduxjs/toolkit";
import data from './data.js'
import student from './student.js'

let notices = createSlice({ 
    name : 'notices',
    initialState : [...data],
    reducers : {
        changeNotices(){
            return '영욱'
        }
    }
})
let students = createSlice({
    name: 'students',
    initialState: [...student],
    reducers : {
    }
})
let show = createSlice({
    name: 'show',
    initialState: false,
    reducers : {
        handleShow(){
            return true;
        },
        handleClose(){
            return false;
        }
    }
})
let dmshow = createSlice({
    name: 'dmshow',
    initialState: null,
    reducers : {
        dmOpen(state,x){
            return x.payload;
        },
        dmClose(){
            return false;
        }
    }
})
let modeValue = createSlice({
    name: 'modeValue',
    initialState: '3',
    reducers : {
        setmodeValue(state,x){
            return x.payload;
        }
    }
})
let calsizeValue = createSlice({
    name: 'calsizeValue',
    initialState: '1',
    reducers : {
        setcalsizeValue(state,x){ return x.payload;}
    }
})
let majorValue = createSlice({
    name: 'majorValue',
    initialState: '1',
    reducers : {
        setmajorValue(state,x){ return x.payload;}
    }
})
let teamValue = createSlice({
    name: 'teamValue',
    initialState: 1,
    reducers : {
        setteamValue(state,x){ return x.payload;}
    }
})

let user = createSlice({
    name: 'user',
    initialState : { id : null, pw: null, 성: null, 이름: null,  team: null, plans:null},
    reducers : {
        userLogin(state,x){return x.payload}
    }
})


export let {changeNotices} = notices.actions
export let {handleClose, handleShow} = show.actions
export let {setmodeValue} = modeValue.actions
export let {setcalsizeValue} = calsizeValue.actions
export let {setmajorValue} = majorValue.actions
export let {setteamValue} = teamValue.actions
export let {userLogin} = user.actions
export let {dmOpen, dmClose} = dmshow.actions

export default configureStore({ //등록하기
    reducer: {
        notices : notices.reducer,
        students : students.reducer,
        show: show.reducer,
        modeValue: modeValue.reducer,
        majorValue: majorValue.reducer,
        calsizeValue: calsizeValue.reducer,
        teamValue: teamValue.reducer,
        user: user.reducer,
        dmshow: dmshow.reducer
    }
})